import React from 'react'
import { Link } from 'react-router-dom'

export default function ContactUS() {
    return (
        <div className="contactus">
            <div className="contactus_top_image">
                <img src="https://flamboyant-neumann-95770b.netlify.app/static/media/contact.bfa91831.jpg" alt="" />
                <img src="https://flamboyant-neumann-95770b.netlify.app/static/media/contact1.ca429960.jpg" alt="" />
            </div>
            <div className="contactus_title_path">
                <h3>Contact Us Areca Nut Leaf Plates in india</h3>
                <div className="contactus_path">
                    <span> <Link to="/" style={{color:'inherit'}}>Home »</Link> </span>
                    <span>contact us</span>
                </div>
            </div>
            <div className="contactus_office_touch">
                <div className="contactus_office">
                    <h4>Office Address</h4>
                    <div className="office_details">
                        <div className="office_address">
                            <div className="office_address_title">
                            Address
                            </div>
                            <div className="office_address_address">
                            No.131/A, SAILAPPA USAN STREET, 1ST FLOOR<br/>
                            AMBUR-635802<br/>
                            TIRUPATHUR DISTRICT<br/>
                            TAMILNADU<br/>
                            INDIA
                            </div>
                        </div>
                        <div className="office_address">
                            <div className="office_address_title">
                            Email
                            </div>
                            <div className="office_address_address">
                            kayeshainternational@gmail.com
                            </div>
                        </div>
                        <div className="office_address">
                            <div className="office_address_title">
                            Mobile
                            </div>
                            <div className="office_address_address">
                            +918056779012
                            </div>
                        </div>
                    </div>
                </div>
                <div className="contactus_touch">
                    <h4>Get in Touch</h4>
                    <div className="touch_form">
                        <form className="form">
                            <input type="text" name="name" placeholder="Name" className="form_input"/>
                            <input type="email" name="email" placeholder="E-mail" className="form_input"/>
                            <input type="tel" name="tel" placeholder="Phone" className="form_input"/>
                            <textarea name="textarea" cols="30" rows="3" placeholder="Message" className="form_textarea"></textarea>
                            <input type="submit" name="submit" value="Submit" className="form_submit"/>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}
