import { Link } from "react-router-dom";

export default function home_section3() {
    return (
        <div className="home_section3">
            <Link className="areca_product" to="/arecaplates">
                <div className="home_section3_title">
                    Areca Products
                </div><br />
                <img src="/Images/Heart_Plate.jpeg" alt="" /><br /><br />
            </Link>
            <Link className="areca_product" to="/duplex">
                <div className="home_section3_title">
                    Paper Products
                </div><br />
                <img src="/Images/Plates.jpeg" alt="" /><br /><br />
            </Link>
        </div>
    );
}