import React from 'react'
import { Link } from 'react-router-dom'

export default function ArecaPlates() {
    return (
        <div className="areca_plates">
            <div className="areca_plates_main_title">
                Areca Plates
            </div>
            <div className="areca_plates_types">
                <div className="areca_plates_type">
                    <div className="areca_plates_type_image">
                        <img src="https://static.wixstatic.com/media/b89fd1_93744812b54a4b8c9e8f64937da7583f~mv2.jpg/v1/fill/w_280,h_270,al_c,q_80,usm_0.66_1.00_0.01/10-inch-round-areca-leaf-plate-500x500_j.webp" alt="" />
                    </div>
                    <div className="areca_plates_type_name">
                        Round Shallow Plates
                    </div>
                    <Link className="areca_plates_type_more" to="/roundshallowplates">
                        Click Here...
                    </Link>
                </div>
                <div className="areca_plates_type">
                    <div className="areca_plates_type_image">
                        <img src="https://static.wixstatic.com/media/b89fd1_ea53479256074ecfb7205dd229e84ce6~mv2.jpg/v1/fill/w_280,h_270,al_c,q_80,usm_0.66_1.00_0.01/56464654456564564_edited.webp" alt="" />
                    </div>
                    <div className="areca_plates_type_name">
                        Round Regular Plates
                    </div>
                    <Link className="areca_plates_type_more" to="/roundregularplates">
                        Click Here...
                    </Link>
                </div>
                <div className="areca_plates_type">
                    <div className="areca_plates_type_image">
                        <img src="https://static.wixstatic.com/media/b89fd1_6f47805dff634faba08a6d6bd72acaba~mv2.png/v1/fill/w_280,h_270,al_c,q_85,usm_0.66_1.00_0.01/546546456456456.webp" alt="" />
                    </div>
                    <div className="areca_plates_type_name">
                        Square Shallow Plates
                    </div>
                    <Link className="areca_plates_type_more" to="/squareshallowplates">
                        Click Here...
                    </Link>
                </div>
            </div>
        </div>
    )
}
